$margin-btw-item: 4px;

.ui-blocks {
  margin-top: 142px;

  &__list {
    display: flex;
    margin-right: -$margin-btw-item;
  }

  &__item {
    width: calc(100% / 2);
    margin-right: $margin-btw-item;
    padding: 63px 0 78px;
    text-align: center;

    &--blue {
      background-color: #0d689e;
    }

    &--light-blue {
      background-color: #179ae8;
    }
  }

  &__name {
    font-family: "book-bold";
    font-size: 51px;
    color: #fff;
    margin-bottom: 30px;
  }
}

@media screen and (max-width: 768px) {
  .ui-blocks {
    &__item {
      padding: 20px;
    }

    &__name {
      font-size: 40px;
    }

    &__button {
      .button--request {
        min-width: initial;
        padding: 14px;
        font-size: 20px;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .ui-blocks {
    &__list {
      flex-direction: column;
    }

    &__item {
      width: 100%;
      margin-bottom: 5px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
