$margin-btw-item: 20px;

.features {
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: -23px;
    margin-top: 115px;
  }

  &__item {
    width: calc((100% / 3) - 18px);
    padding: 48px 28px 49px;
    margin-bottom: 23px;
    color: #fff;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;

    // &:last-child {
    //   margin-right: 0;
    // }

    &--employers {
      background-image: url(../../img/features/empl.png);
    }

    &--best {
      background-image: url(../../img/features/best.png);
    }

    &--career {
      background-image: url(../../img/features/career.png);
    }

    &--expert {
      background-image: url(../../img/features/expert.png);
    }

    &--job {
      background-image: url(../../img/features/job.png);
    }

    &--solutions {
      background-image: url(../../img/features/solutions.png);
    }
  }

  &__icon {
    // display: inline-block;
    // height: 114px;
    // width: 114px;
    // border-radius: 50%;
    // position: relative;
    // background-color: red;
    // margin-bottom: 25px;

    &-img {
      display: inline-block;
      height: 114px;
      width: 114px;
      margin-bottom: 25px;
      // position: absolute;
      // top: 50%;
      // left: 50%;
      // transform: translate(-50%, -50%);
    }
  }

  &__name {
    font-size: 30px;
    font-weight: 700;
    margin-bottom: 20px;
  }

  &__text {
    font-size: 19px;
    line-height: 23px;
  }
}

@media screen and (max-width: 850px) {
  .features {
    &__item {
      padding: 10px;
    }
  }
}

@media screen and (max-width: 768px) {
  .features {
    &__list {
      margin-right: -10px;
    }
    
    &__icon-img{
      height: 80px;
      width: 80px;
    }

    &__item {
      width: 48%;
      margin-right: 10px;
    }

    &__name{
      font-size: 25px;
    }

    &__text{
      font-size: 15px;
    }
  }
}

@media screen and (max-width: 510px) {
  .features {
    &__item{
      width: 100%;
    }
  }
}
