.about {
  margin-top: 48px;
  &__item {
    margin-bottom: 50px;
    display: flex;

    &:last-child {
      margin-bottom: 0;
    }

    &--reverse {
      .about__img {
        margin-right: 0;
        margin-left: 20px;
      }
    }
  }

  &__content {
    margin-top: -12px;
    display: flex;
    flex-direction: column;
  }

  &__img {
    // margin-right: 20px;
    height: 480px;
    width: 632px;
    flex-shrink: 0;
  }

  &__pic {
    max-width: 100%;
    max-height: 100%;
  }

  &__subtitle {
    font-family: "book-bold";
    font-size: 51px;
    color: #0d689e;
    margin-bottom: 26px;
  }

  &__text {
    font-size: 22px;
    color: #053752;
    line-height: 30px;
  }

  &__button {
    margin-top: auto;
  }
}

@media screen and (max-width: 1000px) {
  .about {
    &__img {
      height: 393px;
      width: initial;
    }
  }
}

@media screen and (max-width: 845px) {
  .about {
    &__img {
      height: 300px;
    }
  }
}

@media screen and (max-width: 768px) {
  .about {
    &__item {
      flex-direction: column;
    }

    &__content {
      margin-top: 0;
      text-align: center;
    }

    &__text {
      margin-bottom: 20px;
    }

    &__img {
      text-align: center;
      margin-top: 20px;
    }

    &__button {
      .button--read-more {
        padding: 15px 10px;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .about {

    &__subtitle{
      font-size: 39px;
    }
    
    &__text {
      font-size: 15px;
    }

    &__button {
      .button--read-more {
        max-width: 100%;
        font-size: 24px;
        padding: 10px;
      }
    }
  }
}
