.overlay {
  height: 100%;
  width: 100%;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.4s;

  position: fixed;
  top: 0;
  left: 100%;
  opacity: 0;
  width: 100%;
  height: 100%;
  z-index: 10;

  &__menu {
    .menu {
      &__list {
        display: flex;
        flex-direction: column;
      }

      &__item {
        margin-bottom: 20px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      &__link {
        color: #fff;
        font-size: 25px;
      }
    }
  }
}

.overlay--active {
  left: 0;
  opacity: 0.8;
}
