.banner {
  padding: 70px 0 50px;
  background: rgb(2, 52, 83);
  background: linear-gradient(
    90deg,
    rgba(2, 52, 83, 1) 0%,
    rgba(13, 103, 158, 1) 65%,
    rgba(23, 154, 232, 1) 100%
  );
  margin-top: -37px;

  &__content {
    color: #fff;
    text-align: center;
    padding: 0 13%;
  }

  &__text {
    font-family: "book-bold-italic";
    font-size: 27px;
    line-height: 36px;
    position: relative;

    &::after,
    &::before {
      content: "";
      display: inline-block;
      height: 29px;
      width: 34px;
      background-repeat: no-repeat;
      background-position: center;
      background-image: url(../../img/banner/t.png);
      background-size: cover;
      position: absolute;
      top: -15px;
      left: -32px;
    }

    &::after {
      top: auto;
      bottom: -20px;
      left: auto;
      right: 11px;
      transform: rotate(180deg);
    }
  }

  &__subtext {
    font-size: 21px;
    margin-top: 16px;
  }
}

@media screen and (max-width: 768px) {
  .banner {
    &__text {
      font-size: 23px;

      &::before,
      &::after {
        height: 21px;
        width: 26px;
      }
    }
  }
}

@media screen and (max-width: 430px) {
  .banner {

    &__content{
      padding: 0;
    }
    
    &__text {

      &::before,
      &::after {
        display: none;
      }
    }
  }
}