@font-face {
  // название шрифта(чтобы не запутаться лучше указать название папки со шрифтом)
  font-family: "book-reg";
  // адрес шрифта и тип
  src: url("../fonts/book-reg.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  // название шрифта(чтобы не запутаться лучше указать название папки со шрифтом)
  font-family: "book-bold";
  // адрес шрифта и тип
  src: url("../fonts/book-bold.ttf") format("truetype");
  font-style: normal;
  font-weight: bold;
}

@font-face {
  // название шрифта(чтобы не запутаться лучше указать название папки со шрифтом)
  font-family: "book-italic";
  // адрес шрифта и тип
  src: url("../fonts/book-italic.ttf") format("truetype");
  font-style: italic;
  font-weight: normal;
}

@font-face {
  // название шрифта(чтобы не запутаться лучше указать название папки со шрифтом)
  font-family: "book-bold-italic";
  // адрес шрифта и тип
  src: url("../fonts/book-bold-italic.ttf") format("truetype");
  font-style: italic;
  font-weight: bold;
}

* {
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: black;
}

button,
input {
  border: 1px solid transparent;
  outline: none;
}

button {
  cursor: pointer;
}

.wrapper {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}

.container {
  width: 1200px;
  max-width: 100%;
  margin: 0 auto;
}

.input {
  border-color: #0d689e;
  border-radius: 6px;
  font-size: 16px;
  color: #0d689e;
  padding: 6px 5px;
}

.button {
  font-size: 30px;
  color: #fff;

  &--fot {
    background-color: #fff;
    padding: 9px 8px;
    font-size: 17px;
    color: #0d689e;
  }

  &--request {
    border-radius: 5px;
    border: 4px solid #fff;
    font-weight: 700;
    background-color: transparent;
    min-width: 315px;
    padding: 20px;
  }

  &--read-more {
    font-size: 31px;
    font-weight: 700;
    background-color: red;
    max-width: 316px;
    border-radius: 10px;
    width: 100%;
    padding: 24px 10px;
  }
}

@media screen and (max-width:480px){
  .container{
    padding: 0 2%;
  }
}

.body--active-menu{
  overflow:hidden;

}