.socials {
  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 21px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link{
      display: inline-block;
      background-color: blue;
      height: 30px;
      width: 30px;
      border-radius: 5px;
      position: relative;

      &::after{
        content:'';
        display: inline-block;
        position: absolute;
        height: 20px;
        width: 20px;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        background: url(../../img/fc.png) center no-repeat;
        background-size: cover;
        z-index: 1;
      }
  }
}
