.header {
  padding: 10px 0 53px;
  &__row {
    margin-bottom: 36px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__input {
    .input {
      width: 285px;
      max-width: 100%;
      padding: 12px 5px;
    }
  }
}

@media screen and (max-width: 1050px) {
  .header {
    padding: 10px 4% 53px;
    &__row {
      &:last-child {
        flex-direction: column;
      }
    }

    &__input {
      margin-top: 15px;
    }
  }
}

@media screen and (max-width: 810px) {
  .header {
    padding: 10px 4% 22px;

    &__row {
      &:first-child {
        justify-content: center;
        margin-bottom: 14px;
      }
    }

    &__input {
      margin-top: 0;
    }

    .socials {
      display: none;
    }

    .menu {
      display: none;
    }
  }
}

@media screen and (max-width: 400px) {
  .header {
    padding: 10px 0 22px;

    &__row {
      align-items: flex-start;
      &:first-child {
        justify-content: flex-start;
      }
    }
  }
}
