.footer {
  background: url(../../img/footer.png) center no-repeat;
  background-size: cover;
  padding: 70px 0 140px;
  margin-top: -37px;

  &__row {
    margin-bottom: 71px;
    display: flex;
    // justify-content: space-between;

    &:last-child {
      margin-bottom: 0;
    }

    &--col {
      flex-direction: column;
      align-items: center;
    }
  }

  &__col {
    &:nth-child(1) {
      margin-right: 162px;
    }

    &:nth-child(2) {
      margin-right: 21px;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &__title {
    font-size: 28px;
    font-weight: 700;
    color: #fff;
    margin-bottom: 34px;
    text-transform: uppercase;
  }

  &__categories {
    .menu {
      &__list {
        flex-direction: column;
        align-items: flex-start;
      }

      &__item {
        margin-right: 0;
        margin-bottom: 12px;
      }

      &__link {
        color: #fff;
        font-size: 24px;
      }
    }
  }

  &__contacts {
    .contacts {
      &__address,
      &__mail {
        font-size: 24px;
        font-weight: 400;
        color: #fff;
      }

      &__address {
        margin-bottom: 61px;
        display: flex;

        img {
          height: 20px;
          margin-right: 20px;
        }

        span {
          line-height: 38px;
          margin-top: -8px;
        }
      }

      &__mail {
        img {
          height: 19px;
          margin-right: 6px;
        }
      }
    }
  }

  &__text {
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    text-align: center;
    margin-bottom: 5px;
  }

  &__subtext {
    font-size: 18px;
    font-weight: 400;
    color: #fff;
    margin-bottom: 19px;
  }

  &__buttons {
    margin-top: 15px;
  }

  &__input {
    width: 506px;
    max-width: 100%;
    position: relative;

    .input {
      width: 100%;
      padding: 18px 21px;
      font-size: 20px;
      font-weight: 400;
      color: #0d689e;
      padding-right: 52px;
    }

    &-icon {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 7px;
      height: 42px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .footer {
    margin-top: -57px;

    &__row {
      &--first {
        flex-direction: column;
        align-items: center;
      }
    }

    &__col {
      &:nth-child(1),
      &:nth-child(2),
      &:last-child {
        margin-right: 0;
        text-align: center;
      }
    }

    &__categories {
      .menu {
        &__list {
          align-items: center;
        }
      }
    }

    &__contacts {
      .contacts {
        &__address {
          justify-content: center;
        }
      }
    }
  }
}

@media screen and (max-width: 810px) {
  .footer {
    &__col {
      &--categories {
        display: none;
      }
    }

    &__title {
      font-size: 24px;
    }

    &__contacts {
      .contacts {
        &__address span,
        &__mail span {
          font-size: 20px;
        }
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .footer {
    &__col {
      &--socials {
        display: none;
      }
    }
  }
}
