.about {
  margin-top: 48px;
}

.about__item {
  margin-bottom: 50px;
  display: flex;
}

.about__item:last-child {
  margin-bottom: 0;
}

.about__item--reverse .about__img {
  margin-right: 0;
  margin-left: 20px;
}

.about__content {
  margin-top: -12px;
  display: flex;
  flex-direction: column;
}

.about__img {
  height: 480px;
  width: 632px;
  flex-shrink: 0;
}

.about__pic {
  max-width: 100%;
  max-height: 100%;
}

.about__subtitle {
  font-family: "book-bold";
  font-size: 51px;
  color: #0d689e;
  margin-bottom: 26px;
}

.about__text {
  font-size: 22px;
  color: #053752;
  line-height: 30px;
}

.about__button {
  margin-top: auto;
}

.banner {
  padding: 70px 0 50px;
  background: #023453;
  background: linear-gradient(90deg, #023453 0%, #0d679e 65%, #179ae8 100%);
  margin-top: -37px;
}

.banner__content {
  color: #fff;
  text-align: center;
  padding: 0 13%;
}

.banner__text {
  font-family: "book-bold-italic";
  font-size: 27px;
  line-height: 36px;
  position: relative;
}

.banner__text::after,
.banner__text::before {
  content: "";
  display: inline-block;
  height: 29px;
  width: 34px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../../img/banner/t.png);
  background-size: cover;
  position: absolute;
  top: -15px;
  left: -32px;
}

.banner__text::after {
  top: auto;
  bottom: -20px;
  left: auto;
  right: 11px;
  transform: rotate(180deg);
}

.banner__subtext {
  font-size: 21px;
  margin-top: 16px;
}

.burger {
  position: fixed;
  top: 20px;
  right: 5%;
  height: 25px;
  width: 40px;
  cursor: pointer;
  z-index: 15;
  transition: all 0.4s;
  display: none;
}

.burger__line {
  width: 100%;
  height: 2px;
  position: absolute;
  background-color: black;
  top: 0;
  left: 0;
  transition: all 0.4s;
}

.burger__line:nth-child(2) {
  top: 50%;
  transform: translateY(-50%);
}

.burger__line:nth-child(3) {
  bottom: 0;
  top: initial;
}

.burger--active .burger__line {
  background-color: #fff;
}

.burger--active .burger__line:nth-child(1) {
  opacity: 0;
}

.burger--active .burger__line:nth-child(2) {
  transform: rotate(45deg);
}

.burger--active .burger__line:nth-child(3) {
  transform: rotate(-45deg) translateY(-8px) translateX(5px);
}

.features__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: -23px;
  margin-top: 115px;
}

.features__item {
  width: calc((100% / 3) - 18px);
  padding: 48px 28px 49px;
  margin-bottom: 23px;
  color: #fff;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
}

.features__item--employers {
  background-image: url(../../img/features/empl.png);
}

.features__item--best {
  background-image: url(../../img/features/best.png);
}

.features__item--career {
  background-image: url(../../img/features/career.png);
}

.features__item--expert {
  background-image: url(../../img/features/expert.png);
}

.features__item--job {
  background-image: url(../../img/features/job.png);
}

.features__item--solutions {
  background-image: url(../../img/features/solutions.png);
}

.features__icon-img {
  display: inline-block;
  height: 114px;
  width: 114px;
  margin-bottom: 25px;
}

.features__name {
  font-size: 30px;
  font-weight: 700;
  margin-bottom: 20px;
}

.features__text {
  font-size: 19px;
  line-height: 23px;
}

.footer {
  background: url(../../img/footer.png) center no-repeat;
  background-size: cover;
  padding: 70px 0 140px;
  margin-top: -37px;
}

.footer__row {
  margin-bottom: 71px;
  display: flex;
}

.footer__row:last-child {
  margin-bottom: 0;
}

.footer__row--col {
  flex-direction: column;
  align-items: center;
}

.footer__col:nth-child(1) {
  margin-right: 162px;
}

.footer__col:nth-child(2) {
  margin-right: 21px;
}

.footer__col:last-child {
  margin-right: 0;
}

.footer__title {
  font-size: 28px;
  font-weight: 700;
  color: #fff;
  margin-bottom: 34px;
  text-transform: uppercase;
}

.footer__categories .menu__list {
  flex-direction: column;
  align-items: flex-start;
}

.footer__categories .menu__item {
  margin-right: 0;
  margin-bottom: 12px;
}

.footer__categories .menu__link {
  color: #fff;
  font-size: 24px;
}

.footer__contacts .contacts__address,
.footer__contacts .contacts__mail {
  font-size: 24px;
  font-weight: 400;
  color: #fff;
}

.footer__contacts .contacts__address {
  margin-bottom: 61px;
  display: flex;
}

.footer__contacts .contacts__address img {
  height: 20px;
  margin-right: 20px;
}

.footer__contacts .contacts__address span {
  line-height: 38px;
  margin-top: -8px;
}

.footer__contacts .contacts__mail img {
  height: 19px;
  margin-right: 6px;
}

.footer__text {
  font-size: 20px;
  font-weight: 700;
  color: #fff;
  text-align: center;
  margin-bottom: 5px;
}

.footer__subtext {
  font-size: 18px;
  font-weight: 400;
  color: #fff;
  margin-bottom: 19px;
}

.footer__buttons {
  margin-top: 15px;
}

.footer__input {
  width: 506px;
  max-width: 100%;
  position: relative;
}

.footer__input .input {
  width: 100%;
  padding: 18px 21px;
  font-size: 20px;
  font-weight: 400;
  color: #0d689e;
  padding-right: 52px;
}

.footer__input-icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 7px;
  height: 42px;
}

.header {
  padding: 10px 0 53px;
}

.header__row {
  margin-bottom: 36px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__row:last-child {
  margin-bottom: 0;
}

.header__input .input {
  width: 285px;
  max-width: 100%;
  padding: 12px 5px;
}

.logo__link {
  display: inline-block;
}

.menu__list {
  display: flex;
  align-items: center;
}

.menu__item {
  margin-right: 25px;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__item--active .menu__link {
  font-weight: 700;
}

.menu__link {
  font-size: 18px;
}

.overlay {
  height: 100%;
  width: 100%;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.4s;
  position: fixed;
  top: 0;
  left: 100%;
  opacity: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
}

.overlay__menu .menu__list {
  display: flex;
  flex-direction: column;
}

.overlay__menu .menu__item {
  margin-bottom: 20px;
}

.overlay__menu .menu__item:last-child {
  margin-bottom: 0;
}

.overlay__menu .menu__link {
  color: #fff;
  font-size: 25px;
}

.overlay--active {
  left: 0;
  opacity: 0.8;
}

.preview {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 820px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.preview--main {
  background-image: url(../../img/preview/home.png);
}

.preview__title {
  font-size: 97px;
  font-weight: 700;
  color: #fff;
}

.socials__list {
  display: flex;
  align-items: center;
}

.socials__item {
  margin-right: 21px;
}

.socials__item:last-child {
  margin-right: 0;
}

.socials__link {
  display: inline-block;
  background-color: blue;
  height: 30px;
  width: 30px;
  border-radius: 5px;
  position: relative;
}

.socials__link::after {
  content: '';
  display: inline-block;
  position: absolute;
  height: 20px;
  width: 20px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: url(../../img/fc.png) center no-repeat;
  background-size: cover;
  z-index: 1;
}

.tweet {
  background-color: #0d689e;
  margin-top: 120px;
}

.tweet__img {
  max-width: 100%;
}

.ui-blocks {
  margin-top: 142px;
}

.ui-blocks__list {
  display: flex;
  margin-right: -4px;
}

.ui-blocks__item {
  width: calc(100% / 2);
  margin-right: 4px;
  padding: 63px 0 78px;
  text-align: center;
}

.ui-blocks__item--blue {
  background-color: #0d689e;
}

.ui-blocks__item--light-blue {
  background-color: #179ae8;
}

.ui-blocks__name {
  font-family: "book-bold";
  font-size: 51px;
  color: #fff;
  margin-bottom: 30px;
}

@font-face {
  font-family: "book-reg";
  src: url("../fonts/book-reg.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "book-bold";
  src: url("../fonts/book-bold.ttf") format("truetype");
  font-style: normal;
  font-weight: bold;
}

@font-face {
  font-family: "book-italic";
  src: url("../fonts/book-italic.ttf") format("truetype");
  font-style: italic;
  font-weight: normal;
}

@font-face {
  font-family: "book-bold-italic";
  src: url("../fonts/book-bold-italic.ttf") format("truetype");
  font-style: italic;
  font-weight: bold;
}

* {
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: black;
}

button,
input {
  border: 1px solid transparent;
  outline: none;
}

button {
  cursor: pointer;
}

.wrapper {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}

.container {
  width: 1200px;
  max-width: 100%;
  margin: 0 auto;
}

.input {
  border-color: #0d689e;
  border-radius: 6px;
  font-size: 16px;
  color: #0d689e;
  padding: 6px 5px;
}

.button {
  font-size: 30px;
  color: #fff;
}

.button--fot {
  background-color: #fff;
  padding: 9px 8px;
  font-size: 17px;
  color: #0d689e;
}

.button--request {
  border-radius: 5px;
  border: 4px solid #fff;
  font-weight: 700;
  background-color: transparent;
  min-width: 315px;
  padding: 20px;
}

.button--read-more {
  font-size: 31px;
  font-weight: 700;
  background-color: red;
  max-width: 316px;
  border-radius: 10px;
  width: 100%;
  padding: 24px 10px;
}

.body--active-menu {
  overflow: hidden;
}

@media screen and (max-width: 1100px) {
  .footer {
    margin-top: -57px;
  }

  .footer__row--first {
    flex-direction: column;
    align-items: center;
  }

  .footer__col:nth-child(1),
  .footer__col:nth-child(2),
  .footer__col:last-child {
    margin-right: 0;
    text-align: center;
  }

  .footer__categories .menu__list {
    align-items: center;
  }

  .footer__contacts .contacts__address {
    justify-content: center;
  }
}

@media screen and (max-width: 1050px) {
  .header {
    padding: 10px 4% 53px;
  }

  .header__row:last-child {
    flex-direction: column;
  }

  .header__input {
    margin-top: 15px;
  }
}

@media screen and (max-width: 1000px) {
  .about__img {
    height: 393px;
    width: initial;
  }
}

@media screen and (max-width: 850px) {
  .features__item {
    padding: 10px;
  }
}

@media screen and (max-width: 845px) {
  .about__img {
    height: 300px;
  }
}

@media screen and (max-width: 810px) {
  .burger {
    display: inline-block;
  }

  .footer__col--categories {
    display: none;
  }

  .footer__title {
    font-size: 24px;
  }

  .footer__contacts .contacts__address span,
  .footer__contacts .contacts__mail span {
    font-size: 20px;
  }

  .header {
    padding: 10px 4% 22px;
  }

  .header__row:first-child {
    justify-content: center;
    margin-bottom: 14px;
  }

  .header__input {
    margin-top: 0;
  }

  .header .socials {
    display: none;
  }

  .header .menu {
    display: none;
  }

  .preview {
    min-height: 400px;
  }
}

@media screen and (max-width: 768px) {
  .about__item {
    flex-direction: column;
  }

  .about__content {
    margin-top: 0;
    text-align: center;
  }

  .about__text {
    margin-bottom: 20px;
  }

  .about__img {
    text-align: center;
    margin-top: 20px;
  }

  .about__button .button--read-more {
    padding: 15px 10px;
  }

  .banner__text {
    font-size: 23px;
  }

  .banner__text::before,
  .banner__text::after {
    height: 21px;
    width: 26px;
  }

  .features__list {
    margin-right: -10px;
  }

  .features__icon-img {
    height: 80px;
    width: 80px;
  }

  .features__item {
    width: 48%;
    margin-right: 10px;
  }

  .features__name {
    font-size: 25px;
  }

  .features__text {
    font-size: 15px;
  }

  .ui-blocks__item {
    padding: 20px;
  }

  .ui-blocks__name {
    font-size: 40px;
  }

  .ui-blocks__button .button--request {
    min-width: initial;
    padding: 14px;
    font-size: 20px;
  }
}

@media screen and (max-width: 510px) {
  .features__item {
    width: 100%;
  }
}

@media screen and (max-width: 480px) {
  .about__subtitle {
    font-size: 39px;
  }

  .about__text {
    font-size: 15px;
  }

  .about__button .button--read-more {
    max-width: 100%;
    font-size: 24px;
    padding: 10px;
  }

  .footer__col--socials {
    display: none;
  }

  .preview {
    min-height: 280px;
  }

  .ui-blocks__list {
    flex-direction: column;
  }

  .ui-blocks__item {
    width: 100%;
    margin-bottom: 5px;
  }

  .ui-blocks__item:last-child {
    margin-bottom: 0;
  }

  .container {
    padding: 0 2%;
  }
}

@media screen and (max-width: 430px) {
  .banner__content {
    padding: 0;
  }

  .banner__text::before,
  .banner__text::after {
    display: none;
  }
}

@media screen and (max-width: 400px) {
  .header {
    padding: 10px 0 22px;
  }

  .header__row {
    align-items: flex-start;
  }

  .header__row:first-child {
    justify-content: flex-start;
  }
}