.preview {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 820px;
  position: relative;

  &--main {
    background-image: url(../../img/preview/home.png);
  }

  display: flex;
  align-items: center;
  justify-content: center;

  &__title {
    font-size: 97px;
    font-weight: 700;
    color: #fff;
  }
}

@media screen and (max-width:810px){
  .preview{
    min-height: 400px;
  }
}

@media screen and (max-width:480px){
  .preview{
    min-height: 280px;
  }
}
